<template>
    <div class="post">
        <el-row>
            <el-col :span="4">
                <el-select v-model="event" placeholder="请选择">
                    <el-option
                            v-for="item in events"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-col>
            <el-col :span="4">
                <el-input placeholder="请输入数值A" v-model="number1" type="number"></el-input>
            </el-col>
            <el-col :span="4">
                <el-input placeholder="请输入数值B" v-model="number2" type="number"></el-input>
            </el-col>
            <el-col :span="4">
                <el-button @click="post">发送HTTP请求</el-button>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    export default {
        name:'Post',
        data(){
            return {
                event:'sum',
                events:[
                    {value:'sum',label:'加和'}
                ],
                number1:28,
                number2:108,
            }
        },
        methods:{
            post(){
                this.$emit('post',{number1:this.number1,number2:this.number2,event:this.event})
            }
        }
    }
</script>

<style>
    .post{
        padding: 10px 0 0 0 ;
    }
</style>
