<template>
    <div class="terminal" id="terminal">
        <div class="socketTitle">SOCKET{{notice}}</div>
        <div>
            <div v-for="(i,index) in lists" :key="index">
                <div class="socketMsg" v-if="i.socketMsg">{{i.value}}</div>
                <div class="clientMsg" v-else>{{i.value}}</div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery'
    export default {
        name: 'Terminal',
        data() {
            return {
                lists: [],
                notice: null,
            }
        },
        methods: {
            setClientId(clientId) {
                this.$emit('setClientId', clientId)
            }
        },
        mounted() {
            if ("WebSocket" in window) {
                const ws = new WebSocket("ws://140.143.99.1:7272")
                ws.onopen = () => {
                    this.lists.push({value: '客户端连接SOCKET成功', socketMsg: false})
                }
                ws.onmessage = (e) => {
                    e=JSON.parse(e.data)
                    if (e.clientId) {
                        this.setClientId(e.clientId)
                    } else if (e.type) {
                    } else {
                        this.lists.push({value: e.value, socketMsg: true})
                    }
                }
                ws.onclose = () => {
                    this.lists.push({value: '客户端连接SOCKET关闭', socketMsg: false})
                }
                ws.onerror = () => {
                    this.lists.push({value: '客户端连接SOCKET错误', socketMsg: false})
                }
            } else {
                this.notice = "您的浏览器不支持WebSocket"
            }
        }
    }
</script>

<style>
    .clientMsg{
        padding: 3px 0 3px 0;
        color: #ffb0b6;
    }
    .socketMsg{
        padding: 3px 0 3px 0;
    }
    .terminal {
        background-color: black;
        color: white;
        overflow-y: scroll;
    }

    .socketTitle {
        font-size: 60px;
        text-align: center;
    }
</style>
