<template>
    <div>
        <Terminal :style="{height:terminalHeight+'px',width:'100%'}" @setClientId="setClientId"></Terminal>
        <Post :style="{height:postHeight+'px',width:'100%'}" @post="post"></Post>
        <Iframe ref="iframe" :style="{height:iframeHeight+'px',width:'100%'}"></Iframe>
    </div>
</template>

<script>
    import $ from 'jquery'
    import Terminal from '../components/Terminal.vue'
    import Post from '../components/Post.vue'
    import Iframe from '../components/Iframe.vue'

    export default {
        name: 'Home',
        methods: {
            setClientId(clientId) {
                this.clientId = clientId
            },
            post(params) {
                const msg = JSON.stringify({
                    requestId: this.randomString(30),
                    event: params.event,
                    number1: params.number1,
                    number2: params.number2,
                    clientId: this.clientId,
                })
                this.$refs.iframe.src = 'http://140.143.99.1:8009/send.php?msg=' + encodeURIComponent(msg)
            },
            randomString(len) {
                const chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz';
                const maxPos = chars.length;
                let pwd = '';
                for (let i = 0; i < len; i++) {
                    pwd += chars.charAt(Math.floor(Math.random() * maxPos));
                }
                return pwd;
            },
        },
        data() {
            return {
                //number
                windowHeight: null,
                //number
                terminalHeight: null,
                //number
                iframeHeight: null,
                //post number
                postHeight: null,
                clientId: null,
                //url
                url: null,

            }
        },
        mounted() {
            this.$nextTick(function () {
                this.windowHeight = $(window).height()
                this.terminalHeight = this.windowHeight * 0.5
                this.postHeight = this.windowHeight * 0.1
                this.iframeHeight = this.windowHeight * 0.4
            })
        },
        components: {
            Terminal,
            Post,
            Iframe
        },

    }
</script>
